declare module "vue/types/vue" {
  interface Vue {}
}
@Component({
  name: "Home",
  components: {}
})
export default class Home extends Vue {
  // constructor() {
  //   super()
  // }
  //
  private loginDate: string = "";

  private mounted() {
    let _this = this;
    const DateFormat = this.DateFormat.format;
    setInterval(() => {
      _this.loginDate =
        DateFormat(`yyyy 年 MM 月 dd 日 HH:mm:ss`) +
        " " +
        "星期" +
        "日一二三四五六".charAt(new Date().getDay());
    }, 1000);
  }
  public loginLoading?: boolean = this.loading;
  private dark: boolean = false;
  private primaryDrawer: object = {
    model: false,
    type: "default (no property)",
    clipped: true,
    floating: true,
    mini: false
  };
  private items: object[] = [
    { title: "首页", icon: "mdi-nintendo-switch", link: "/home/about" },
    { title: "ECharts", icon: "mdi-chart-bell-curve", link: "/themeIndex" },
    { title: "博客", icon: "mdi-chili-hot", link: "/blogIndex" }
  ];

  private handleNavBlur(): void {
    // this.primaryDrawer.model = false
  }
  // 当有组件更新时则进入这个钩子
  async beforeUpdate() {
    /*关闭头顶加载*/
    this.loginLoading = false;
  }
}
